<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-company-branches',
  data () {
    return {
      config: {
        title: 'Kantor Cabang Perusahaan',
        model_api: 'company-branches',
        getter: 'company-branches',
        setter: 'company-branches',
        pk_field: null,
        permission: {
          create: 'create-company-branches',
          read: 'view-company-branches',
          update: 'update-company-branches',
          delete: 'delete-company-branches'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'company_branch_name', label: 'Nama Cabang' },
          { id: 'company_type', label: 'Jenis Perusahaan' },
          { id: 'nomor_nib', label: 'Nomor NIB' },
          { id: 'nomor_npwp', label: 'Nomor NPWP' },
          { id: 'primary_address', label: 'Alamat Utama', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'secondary_address', label: 'Alamat Sekunder', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'postal_code', label: 'Kode POS' },
          { id: 'primary_telephone', label: 'No. Telp Utama' },
          { id: 'secondary_telephone', label: 'No. Telp Sekunder' },
          { id: 'url_website', label: 'Website Perusahaan' },
          {
            id: 'img_logo',
            label: 'Foto Tenaga Kerja',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_logo.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }],
                view_data: 'img_logo'
              }
            }
          },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



